























import { defineComponent, ref } from "@vue/composition-api";

export default defineComponent({
  name: "FailDialog",
  setup(_props, { emit }) {
    const checked = ref(false);
    function confirmClick() {
      emit("confirm");
    }
    function cancelClick() {
      emit("close");
    }
    return { checked, confirmClick, cancelClick };
  }
});
