












































import { computed, defineComponent, Ref, ref } from "@vue/composition-api";
import Axios, { AxiosResponse } from "axios";
import CircularProgress from "../components/CircularProgress.vue";

export default defineComponent({
  name: "Home",
  props: ["semesters"],
  components: { CircularProgress },
  setup(props, { emit }) {
    const selectedSemesterCourses: Ref<Course[]> = ref([]);
    const selcetedCourseSpreadsheetsID = ref("");
    const password = ref("");
    const checkInput = computed(
      () => selcetedCourseSpreadsheetsID.value !== ""
    );
    const authToken = ref("");
    const verifying = ref(false);

    async function verify() {
      let data: AxiosResponse<{ authToken: string; semesters: Semester[] }>;
      verifying.value = true;
      try {
        data = await Axios.post(
          "https://script.google.com/macros/s/AKfycbyj-Vl_UrnG9jRrt3BBD9kNew0PhnXVKVQgXTeP/exec",
          { password: password.value },
          {
            headers: {
              "Content-Type": "text/plain"
            }
          }
        );
      } catch (e) {
        verifying.value = false;
        alert("通關密語錯誤");
        return;
      }
      verifying.value = false;
      emit("verified", data.data.authToken, data.data.semesters);
    }

    function goClick() {
      emit("go", selcetedCourseSpreadsheetsID);
    }

    return {
      goClick,
      selectedSemesterCourses,
      selcetedCourseSpreadsheetsID,
      password,
      checkInput,
      verify,
      authToken,
      verifying
    };
  }
});

interface Course {
  name: string;
  spreadsheetsID: string;
}

interface Semester {
  name: string;
  activeCourses: Course[];
}
