



















import { defineComponent, ref } from "@vue/composition-api";
import QRCodeScanner from "../components/QRCodeScanner.vue";
import SuccessDialog from "../components/SuccessDialog.vue";
import FailDialog from "../components/FailDialog.vue";
import CircularProgress from "../components/CircularProgress.vue";
import Axios, { AxiosResponse } from "axios";

export default defineComponent({
  name: "ScanPage",
  props: ["authToken", "spreadsheetsID"],
  components: {
    QRCodeScanner,
    SuccessDialog,
    FailDialog,
    CircularProgress
  },
  setup(props, { emit }) {
    const failDialog = ref(false);
    const successDialog = ref(false);
    const camera = ref("auto");
    const waiting = ref(false);
    let tmpStudentToken = "";

    function previousPage() {
      emit("previousPage");
    }
    async function signin(studentToken: string, paid?: any) {
      camera.value = "off";
      waiting.value = true;
      let data: AxiosResponse<{ success: boolean }>;
      try {
        data = await Axios.post(
          "https://script.google.com/macros/s/AKfycbxWVyyeUqu2HvGtLBqctPtMkptXeqzvaKPD9LLZ6Wvn7-lV6Vzp/exec",
          {
            authToken: props.authToken,
            spreadsheetsID: props.spreadsheetsID.value,
            studentToken: studentToken,
            paid
          },
          {
            headers: {
              "Content-Type": "text/plain"
            }
          }
        );
      } catch (e) {
        waiting.value = false;
        tmpStudentToken = studentToken;
        failDialog.value = true;
        return;
      }
      if (!data.data.success) {
        waiting.value = false;
        tmpStudentToken = studentToken;
        failDialog.value = true;
      } else {
        waiting.value = false;
        successDialog.value = true;
      }
    }

    function successClose() {
      successDialog.value = false;
      camera.value = "auto";
    }
    function failClose() {
      failDialog.value = false;
      camera.value = "auto";
    }
    function failConfirm() {
      failDialog.value = false;
      signin(tmpStudentToken, true);
    }

    return {
      failDialog,
      successDialog,
      signin,
      camera,
      successClose,
      failClose,
      waiting,
      previousPage,
      failConfirm
    };
  }
});
